.header { background: -webkit-gradient(linear, left top, right top, from(#0f2456), to(#546dd0)); background: -webkit-linear-gradient(left, #0f2456, #546dd0); background: -o-linear-gradient(left, #0f2456, #546dd0); background: linear-gradient(to right, #0f2456, #546dd0);}

.header .container{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
    -webkit-box-shadow: none;
            box-shadow: none;
    padding: 0;
    color: white;
}
.navbar-brand p {
  font-size: 24px;
  font-family: 'Pacifico';
  opacity: .8;
  display: -ms-flexbox;
  display: flex;
  margin: 10px 40px 8px 0;
}
.justify-content-between {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
}
