	display: -ms-flexbox;
	display: flex;
}

.custom-navbar-form input.form-control.form-control-sm {
	width: 200px;
	display: -ms-flexbox;
	display: flex;
	border-radius: 0;
	background: none;
	border: none;
	border-bottom: 1px solid white;
}

.custom-navbar-nav.nav.nav-pills { -ms-flex-align: center; align-items: center;}
.custom-navbar-nav.nav.nav-pills .navbar-link a {
	color: white;
	padding: 20px 10px;
}

.custom-navbar-nav.nav.nav-pills .navbar-link a:hover { color: #00BDD4;}

#menu-search-input {
	color: white;
}


