
.footer {
	margin-top: 80px;
	background: -webkit-gradient(linear, left top, right top, from(#0f2456), to(#546dd0));
	background: -webkit-linear-gradient(left, #0f2456, #546dd0);
	background: -o-linear-gradient(left, #0f2456, #546dd0);
	background: linear-gradient(to right, #0f2456, #546dd0);
	padding: 40px 0;
	color: white;
}

.footer h5 { margin-bottom: 30px;}

.branding h5 {
  font-size: 24px;
  font-family: 'Pacifico';
  opacity: .8;
  -webkit-box-shadow: none;
          box-shadow: none;
}

.footer p { font-size: 13px; color: lightgrey;}
.footer a { font-size: 13px; color: white;}
.footer a:hover { color: #00BDD4;}

.footer hr { background: -webkit-gradient(linear, right top, left top, from(#0f2456), to(#546dd0)); background: -webkit-linear-gradient(right, #0f2456, #546dd0); background: -o-linear-gradient(right, #0f2456, #546dd0); background: linear-gradient(to left, #0f2456, #546dd0);}



